import React from "react";
import video from "./particles.mp4";
import image from "./astroImage.svg";
import "./Header.scss";

const Header = () => {
  return (
    <div className="header">
      <video autoPlay muted loop className="spaceVideo">
        <source src={video} type="video/mp4" />
      </video>
      <img src={image} alt="astro" />
      <div className="titles">
        <h1 className="title">Welcome to SpaceXformation</h1>
        <h2 className="subtitle">Click here to explore spaceX missions</h2>
      </div>
    </div>
  );
};

export default Header;
